// ArtboardViewModel.ts

import { useState } from 'react';
import { Path, PathUtils } from '../lib/geometry/Path';
import { Vertex } from '../lib/math/Vertex';
import { CapType } from '../lib/geometry/CapType';
import { CornerType } from '../lib/geometry/CornerType';
import { Dimensions } from '../lib/math/Dimensions';
import { ArtboardObject } from '../lib/ArtboardObject';
import { Point } from '../lib/math/Point';
import { ImageObject } from '../lib/object/ImageObject';
import { ScaleOffset } from '../lib/math/ScaleOffset';

export interface ArtboardState {
  boardDimensions: Dimensions;
  scaleOffset: ScaleOffset;
  objects: ArtboardObject[];
  selectedObjectIndex: number | null;
  selectedVertexIndex: number | null;
  hoveredObjectIndex: number | null;
  hoveredVertexIndex: number | null;
}

export class ArtboardViewModel {
  private _state: ArtboardState;
  private setState: React.Dispatch<React.SetStateAction<ArtboardState>>;

  constructor(
    initialState: ArtboardState,
    setState: React.Dispatch<React.SetStateAction<ArtboardState>>,
  ) {
    this._state = initialState;
    this.setState = setState;
  }

  get state() {
    return this._state;
  }

  get selectedObject(): ArtboardObject | null {
    return this._state.selectedObjectIndex !== null
      ? this._state.objects[this._state.selectedObjectIndex]
      : null;
  }

  get selectedPath(): Path | null {
    const obj = this.selectedObject;
    return obj && this.isPath(obj) ? obj : null;
  }

  public copySelectedObject = () => {
    if (this.state.selectedObjectIndex === null) {
      return;
    }
    const selectedObject = this.state.objects[this.state.selectedObjectIndex];
    const serializedData = JSON.stringify(selectedObject);

    // Write to clipboard
    if ('clipboard' in navigator && 'writeText' in navigator.clipboard) {
      navigator.clipboard.writeText(serializedData).then(
        () => {
          console.log('Copied to clipboard');
        },
        (err) => {
          console.error('Failed to copy: ', err);
        },
      );
    } else {
      console.warn('Clipboard API not supported');
    }
  };

  public pasteFromClipboard = async () => {
    try {
      if ('clipboard' in navigator && 'read' in navigator.clipboard) {
        const clipboardItems = await navigator.clipboard.read();
        for (const item of clipboardItems) {
          for (const type of item.types) {
            if (type.startsWith('image/')) {
              const blob = await item.getType(type);
              const img = new Image();
              const url = URL.createObjectURL(blob);
              img.onload = () => {
                URL.revokeObjectURL(url);
                const imageObject: ImageObject = {
                  image: img,
                  scaleOffset: new ScaleOffset(
                    (this.state.boardDimensions.w - img.width) / 2,
                    (this.state.boardDimensions.h - img.height) / 2,
                  ),
                  opacity: 1,
                  zIndex: this.state.objects.length,
                  visible: true,
                  locked: false,
                };
                this.addObject(imageObject);
              };
              img.src = url;
              return;
            }
          }

          // Try to read text data
          const textBlob = await item.getType('text/plain');
          const textData = await textBlob.text();
          if (textData) {
            try {
              const parsedObject = JSON.parse(textData) as ArtboardObject;
              parsedObject.scaleOffset = new ScaleOffset(
                (this.state.boardDimensions.w - 100) / 2,
                (this.state.boardDimensions.h - 100) / 2,
              );
              parsedObject.zIndex = this.state.objects.length;
              this.addObject(parsedObject);
            } catch (err) {
              console.error('Failed to parse clipboard text: ', err);
            }
          }
        }
      } else if ('clipboard' in navigator && 'readText' in navigator.clipboard) {
        // Fallback to readText
        const textData = await navigator.clipboard.readText();
        if (textData) {
          try {
            const parsedObject = JSON.parse(textData) as ArtboardObject;
            parsedObject.scaleOffset = new ScaleOffset(
              (this.state.boardDimensions.w - 100) / 2,
              (this.state.boardDimensions.h - 100) / 2,
            );
            parsedObject.zIndex = this.state.objects.length;
            this.addObject(parsedObject);
          } catch (err) {
            console.error('Failed to parse clipboard text: ', err);
          }
        }
      } else {
        console.warn('Clipboard API not supported');
      }
    } catch (err) {
      console.error('Failed to read from clipboard: ', err);
    }
  };

  addObject = (newObject: ArtboardObject) => {
    this.setState((prevState) => {
      // Assign the new object a zIndex higher than any existing object
      const highestZIndex = prevState.objects.reduce((max, obj) => Math.max(max, obj.zIndex), 0);
      newObject.zIndex = highestZIndex + 1;

      return {
        ...prevState,
        objects: [...prevState.objects, newObject],
      };
    });
  };

  updateObjects = (newObjects: ArtboardObject[]) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        objects: [...newObjects],
      };
    });
  };

  // Helper method to check if an object is a Path
  isPath = (object: ArtboardObject): object is Path => {
    return (object as Path).vertices !== undefined;
  };

  // Helper method to check if an object is an ImageObject
  isImageObject = (object: ArtboardObject): object is ImageObject => {
    return (object as ImageObject).image !== undefined;
  };

  // Methods for Paths
  updateFillColor = (color: string) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, fill: color };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateStrokeColor = (color: string) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, strokeColor: color };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateStrokeWidth = (value: number) => {
    if (isNaN(value)) return;
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (!this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, strokeWidth: value };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateCapType = (cap: CapType) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, cap: cap };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateCornerType = (corner: CornerType) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, corner: corner };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateObjectVisibility = (visible: boolean) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      const updatedObject = { ...obj, visible: visible };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedObject;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateObjectVisibilityAtIndex = (index: number, visible: boolean) => {
    this.setState((prevState) => {
      const { objects } = prevState;

      // Check if the index is valid
      if (index < 0 || index >= objects.length) {
        return prevState; // Return the previous state if the index is out of bounds
      }

      const obj = objects[index];
      const updatedObject = { ...obj, visible: visible };
      const updatedObjects = objects.slice();
      updatedObjects[index] = updatedObject;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateObjectLocked = (locked: boolean) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      const updatedObject = { ...obj, locked: locked };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedObject;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateObjectLockedAtIndex = (index: number, locked: boolean) => {
    this.setState((prevState) => {
      const { objects } = prevState;

      // Check if the index is valid
      if (index < 0 || index >= objects.length) {
        return prevState; // Return the previous state if the index is out of bounds
      }

      const obj = objects[index];
      const updatedObject = { ...obj, locked: locked };
      const updatedObjects = objects.slice();
      updatedObjects[index] = updatedObject;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateOpacity = (opacity: number) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked) return prevState;
      const updatedObject = { ...obj, opacity: opacity };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedObject;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateFillOpacity = (opacity: number) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, fillOpacity: opacity };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  updateStrokeOpacity = (opacity: number) => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const obj = objects[selectedObjectIndex];
      if (obj.locked || !this.isPath(obj)) {
        return prevState;
      }

      const updatedPath = { ...obj, strokeOpacity: opacity };
      const updatedObjects = objects.slice();
      updatedObjects[selectedObjectIndex] = updatedPath;

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  clearCurrentObject = () => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (
        selectedObjectIndex === null ||
        selectedObjectIndex < 0 ||
        selectedObjectIndex >= objects.length
      ) {
        return prevState;
      }

      const updatedObjects = objects.filter((_, index) => index !== selectedObjectIndex);

      return {
        ...prevState,
        objects: updatedObjects,
        selectedObjectIndex: null,
      };
    });
  };

  // Z-index methods
  moveObjectUp = () => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (selectedObjectIndex === null) return prevState;

      const selectedObject = objects[selectedObjectIndex];
      if (selectedObject.locked) return prevState;
      const maxZIndex = Math.max(...objects.map((obj) => obj.zIndex));

      if (selectedObject.zIndex >= maxZIndex) return prevState; // Already at the top

      // Increase the zIndex of the selected object
      const updatedObjects = objects.map((obj) => {
        if (obj === selectedObject) {
          return { ...obj, zIndex: obj.zIndex + 1 };
        } else if (obj.zIndex === selectedObject.zIndex + 1) {
          // Decrease zIndex of the object just above
          return { ...obj, zIndex: obj.zIndex - 1 };
        }
        return obj;
      });

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  moveObjectDown = () => {
    this.setState((prevState) => {
      const { selectedObjectIndex, objects } = prevState;

      if (selectedObjectIndex === null) return prevState;

      const selectedObject = objects[selectedObjectIndex];
      if (selectedObject.locked) return prevState;
      const minZIndex = Math.min(...objects.map((obj) => obj.zIndex));

      if (selectedObject.zIndex <= minZIndex) return prevState; // Already at the bottom

      // Decrease the zIndex of the selected object
      const updatedObjects = objects.map((obj) => {
        if (obj === selectedObject) {
          return { ...obj, zIndex: obj.zIndex - 1 };
        } else if (obj.zIndex === selectedObject.zIndex - 1) {
          // Increase zIndex of the object just below
          return { ...obj, zIndex: obj.zIndex + 1 };
        }
        return obj;
      });

      return {
        ...prevState,
        objects: updatedObjects,
      };
    });
  };

  // Update methods for selection and hovering
  updateSelectedObjectIndex = (index: number | null) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        selectedObjectIndex: index,
      };
    });
  };

  updateSelectedVertexIndex = (index: number | null) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        selectedVertexIndex: index,
      };
    });
  };

  updateHoveredObjectIndex = (index: number | null) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        hoveredObjectIndex: index,
      };
    });
  };

  updateHoveredVertexIndex = (index: number | null) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        hoveredVertexIndex: index,
      };
    });
  };

  updateZoomScale = (scale: number) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        scaleOffset: { ...prevState.scaleOffset, scaleX: scale, scaleY: scale },
      };
    });
  };

  updatePanX = (x: number) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        scaleOffset: { ...prevState.scaleOffset, x: x },
      };
    });
  };

  updatePanY = (y: number) => {
    this.setState((prevState) => {
      return {
        ...prevState,
        scaleOffset: { ...prevState.scaleOffset, y: y },
      };
    });
  };
}

export const useArtboardViewModel = () => {
  const [artboardState, setArtboardState] = useState<ArtboardState>({
    boardDimensions: new Dimensions(800, 600),
    scaleOffset: new ScaleOffset(0, 0, 1, 1),
    objects: [
      PathUtils.createInstance(
        [
          new Vertex(new Point(100, 100), null, new Point(150, 50), true),
          new Vertex(new Point(200, 100), new Point(150, 150), new Point(250, 50), true),
          new Vertex(new Point(300, 100), new Point(250, 150), null, true),
        ],
        0, // initial zIndex
      ),
    ],
    selectedObjectIndex: null,
    selectedVertexIndex: null,
    hoveredObjectIndex: null,
    hoveredVertexIndex: null,
  });

  return new ArtboardViewModel(artboardState, setArtboardState);
};
