// SelectionTool.ts

import { Path } from '../geometry/Path';
import { Point } from '../math/Point';
import { Vertex } from '../math/Vertex';
import { ImageObject } from '../object/ImageObject';
import { Tool, ToolContext } from './Tool';
import { ScaleOffset } from '../math/ScaleOffset';
import { ArtboardObject } from '../ArtboardObject';

type DraggingType = null | {
  objectIndex: number;
  initialMousePos: { x: number; y: number };
  initialOffset: { x: number; y: number };
  initialScale?: { scaleX: number; scaleY: number }; // For images
  initialVertices?: Vertex[]; // For paths
  action: 'move' | 'resize';
  handle?: ResizeHandle; // For resizing, which handle is being dragged
};

type ResizeHandle = 'nw' | 'n' | 'ne' | 'e' | 'se' | 's' | 'sw' | 'w';

export class SelectionTool implements Tool {
  private dragging: DraggingType = null;
  private handleSize = 8; // Size of the resize handles in pixels

  onMouseDown(event: MouseEvent, context: ToolContext): void {
    const { x, y } = this.getMousePos(event, context);
    const { artboardViewModel } = context;

    if (artboardViewModel.state.selectedObjectIndex !== null) {
      const obj = artboardViewModel.state.objects[artboardViewModel.state.selectedObjectIndex];
      if (obj.locked) {
        this.dragging = null;
        return;
      }

      // First, check if the mouse is over a resize handle
      const handle = this.getHandleUnderMouse(event, context, obj);
      if (handle) {
        // Start resizing
        const initialOffset = { x: obj.scaleOffset.x, y: obj.scaleOffset.y };
        const draggingData: DraggingType = {
          objectIndex: artboardViewModel.state.selectedObjectIndex,
          initialMousePos: { x, y },
          initialOffset,
          action: 'resize',
          handle,
        };

        if (artboardViewModel.isImageObject(obj)) {
          draggingData.initialScale = {
            scaleX: obj.scaleOffset.scaleX,
            scaleY: obj.scaleOffset.scaleY,
          };
        } else if (artboardViewModel.isPath(obj)) {
          // Deep copy the vertices
          draggingData.initialVertices = obj.vertices.map((v) => v.clone());
        }

        this.dragging = draggingData;
        return;
      }
    }

    // If not over a resize handle, proceed with selection/move logic

    // Collect objects with their bounding boxes
    const objectData = artboardViewModel.state.objects
      .map((obj, index) => {
        if (artboardViewModel.isPath(obj)) {
          const path = obj;
          const boundingBox = this.calculateBezierBoundingBox(path);

          // Apply offset and scale
          const minX = boundingBox.minX * path.scaleOffset.scaleX + path.scaleOffset.x;
          const minY = boundingBox.minY * path.scaleOffset.scaleY + path.scaleOffset.y;
          const maxX = boundingBox.maxX * path.scaleOffset.scaleX + path.scaleOffset.x;
          const maxY = boundingBox.maxY * path.scaleOffset.scaleY + path.scaleOffset.y;

          return {
            object: obj,
            index,
            zIndex: obj.zIndex,
            boundingBox: { minX, minY, maxX, maxY },
          };
        } else if (artboardViewModel.isImageObject(obj)) {
          const imageObj = obj;
          const minX = imageObj.scaleOffset.x;
          const minY = imageObj.scaleOffset.y;
          const maxX = imageObj.scaleOffset.x + imageObj.image.width * imageObj.scaleOffset.scaleX;
          const maxY = imageObj.scaleOffset.y + imageObj.image.height * imageObj.scaleOffset.scaleY;

          return {
            object: obj,
            index,
            zIndex: obj.zIndex,
            boundingBox: { minX, minY, maxX, maxY },
          };
        } else {
          return null;
        }
      })
      .filter(Boolean) as {
      object: Path | ImageObject;
      index: number;
      zIndex: number;
      boundingBox: { minX: number; minY: number; maxX: number; maxY: number };
    }[];

    // Sort objects by zIndex in descending order
    objectData.sort((a, b) => b.zIndex - a.zIndex);

    // Check if any object is under the mouse
    for (const data of objectData) {
      if (data.object.locked) continue;
      const { index, boundingBox } = data;
      const { minX, minY, maxX, maxY } = boundingBox;

      if (x >= minX && x <= maxX && y >= minY && y <= maxY) {
        // Object is under the mouse
        artboardViewModel.updateSelectedObjectIndex(index);

        // Start dragging
        const obj = artboardViewModel.state.objects[index];
        const initialOffset = { x: obj.scaleOffset.x, y: obj.scaleOffset.y };

        this.dragging = {
          objectIndex: index,
          initialMousePos: { x, y },
          initialOffset,
          action: 'move',
        };

        return;
      }
    }

    // No object under mouse
    artboardViewModel.updateSelectedObjectIndex(null);
    this.dragging = null;
  }

  onMouseMove(event: MouseEvent, context: ToolContext): boolean {
    if (!this.dragging) return false;

    const { x, y } = this.getMousePos(event, context);
    const { artboardViewModel } = context;

    const dx = x - this.dragging.initialMousePos.x;
    const dy = y - this.dragging.initialMousePos.y;

    const updatedObjects = [...artboardViewModel.state.objects];
    const objectIndex = this.dragging.objectIndex;
    const obj = updatedObjects[objectIndex];

    // Create copies of the scaleOffset to avoid mutating the original directly
    let updatedX = obj.scaleOffset.x;
    let updatedY = obj.scaleOffset.y;
    let updatedScaleX = obj.scaleOffset.scaleX;
    let updatedScaleY = obj.scaleOffset.scaleY;

    if (this.dragging.action === 'move') {
      // Move the object
      updatedX = this.dragging.initialOffset.x + dx;
      updatedY = this.dragging.initialOffset.y + dy;
    } else if (this.dragging.action === 'resize') {
      // Resize the object
      const handle = this.dragging.handle!;
      const initialBoundingBox = this.getObjectBoundingBox(obj);

      const fixedPoint = this.getFixedPoint(initialBoundingBox, handle);
      const movingPoint = { x, y };

      // Calculate scale factors based on the movement
      let scaleXFactor = 1;
      let scaleYFactor = 1;

      const MIN_DIMENSION = 1e-6;

      const initialWidth = this.dragging.initialMousePos.x - fixedPoint.x;
      const initialHeight = this.dragging.initialMousePos.y - fixedPoint.y;

      const currentWidth = movingPoint.x - fixedPoint.x;
      const currentHeight = movingPoint.y - fixedPoint.y;

      // Handle orthogonal and corner handles
      if (handle === 'n' || handle === 's') {
        // Only scale Y
        if (Math.abs(initialHeight) > MIN_DIMENSION) {
          scaleYFactor = currentHeight / initialHeight;
        } else {
          scaleYFactor = currentHeight >= 0 ? 1 : -1;
        }
      } else if (handle === 'e' || handle === 'w') {
        // Only scale X
        if (Math.abs(initialWidth) > MIN_DIMENSION) {
          scaleXFactor = currentWidth / initialWidth;
        } else {
          scaleXFactor = currentWidth >= 0 ? 1 : -1;
        }
      } else {
        // Corner handles, scale both X and Y
        if (Math.abs(initialWidth) > MIN_DIMENSION) {
          scaleXFactor = currentWidth / initialWidth;
        } else {
          scaleXFactor = currentWidth >= 0 ? 1 : -1;
        }
        if (Math.abs(initialHeight) > MIN_DIMENSION) {
          scaleYFactor = currentHeight / initialHeight;
        } else {
          scaleYFactor = currentHeight >= 0 ? 1 : -1;
        }
      }

      // Clamp scaling factors to prevent extreme values
      const MAX_SCALE = 1000;
      const MIN_SCALE = -1000;

      scaleXFactor = Math.max(Math.min(scaleXFactor, MAX_SCALE), MIN_SCALE);
      scaleYFactor = Math.max(Math.min(scaleYFactor, MAX_SCALE), MIN_SCALE);

      if (artboardViewModel.isImageObject(obj)) {
        // For images, adjust scaleX and scaleY
        updatedScaleX = this.dragging.initialScale!.scaleX * scaleXFactor;
        updatedScaleY = this.dragging.initialScale!.scaleY * scaleYFactor;

        // Adjust position
        const deltaX = fixedPoint.x - fixedPoint.x * scaleXFactor;
        const deltaY = fixedPoint.y - fixedPoint.y * scaleYFactor;

        updatedX = this.dragging.initialOffset.x + deltaX;
        updatedY = this.dragging.initialOffset.y + deltaY;
      } else if (artboardViewModel.isPath(obj)) {
        // For paths, adjust vertices and control points
        const initialVertices = this.dragging.initialVertices!;
        const newVertices = initialVertices.map((v) => {
          const newBaseX = fixedPoint.x + (v.base.x - fixedPoint.x) * scaleXFactor;
          const newBaseY = fixedPoint.y + (v.base.y - fixedPoint.y) * scaleYFactor;

          const newControlLeft = v.controlLeft
            ? new Point(
                fixedPoint.x + (v.controlLeft.x - fixedPoint.x) * scaleXFactor,
                fixedPoint.y + (v.controlLeft.y - fixedPoint.y) * scaleYFactor,
              )
            : null;

          const newControlRight = v.controlRight
            ? new Point(
                fixedPoint.x + (v.controlRight.x - fixedPoint.x) * scaleXFactor,
                fixedPoint.y + (v.controlRight.y - fixedPoint.y) * scaleYFactor,
              )
            : null;

          return new Vertex(
            new Point(newBaseX, newBaseY),
            newControlLeft,
            newControlRight,
            v.linkedHandles,
          );
        });

        obj.vertices = newVertices;

        // No need to adjust scaleOffset.scaleX and scaleY for paths
      }
    }

    // Update the object's scaleOffset
    obj.scaleOffset = new ScaleOffset(updatedX, updatedY, updatedScaleX, updatedScaleY);

    updatedObjects[objectIndex] = obj;
    artboardViewModel.updateObjects(updatedObjects);
    return true; // Return true to indicate canvas needs to rerender
  }

  onMouseUp(event: MouseEvent, context: ToolContext): void {
    this.dragging = null;
  }

  drawOverlay(context: ToolContext, ctx: CanvasRenderingContext2D): void {
    const { canvas, artboardViewModel } = context;

    if (artboardViewModel.state.selectedObjectIndex === null) return;

    const obj = artboardViewModel.state.objects[artboardViewModel.state.selectedObjectIndex];

    // Get scale and translation from artboardViewModel
    const scaleOffset = artboardViewModel.state.scaleOffset;
    const artboardDimensions = artboardViewModel.state.boardDimensions;

    // Apply the same transformations as in MainCanvas
    const rect = canvas.getBoundingClientRect();

    ctx.save();

    // Apply pan and zoom transformations
    ctx.translate(scaleOffset.x, scaleOffset.y);
    ctx.scale(scaleOffset.scaleY, scaleOffset.scaleY);

    // Center the artboard within the canvas
    const xOffset = (rect.width / scaleOffset.scaleY - artboardDimensions.w) / 2;
    const yOffset = (rect.height / scaleOffset.scaleY - artboardDimensions.h) / 2;

    ctx.translate(xOffset, yOffset);

    const boundingBox = this.getObjectBoundingBox(obj);

    // Draw the bounding box
    ctx.strokeStyle = '#0000FF'; // Dark blue
    ctx.lineWidth = 1 / scaleOffset.scaleY;
    ctx.setLineDash([4 / scaleOffset.scaleY, 2 / scaleOffset.scaleY]); // Dashed line
    ctx.strokeRect(
      boundingBox.minX,
      boundingBox.minY,
      boundingBox.maxX - boundingBox.minX,
      boundingBox.maxY - boundingBox.minY,
    );
    ctx.setLineDash([]);

    // Draw resize handles
    const handles = this.getResizeHandles(boundingBox);
    ctx.fillStyle = '#FFFFFF';
    ctx.strokeStyle = '#000000';
    ctx.lineWidth = 1 / scaleOffset.scaleY;

    const handleSize = this.handleSize / scaleOffset.scaleY;

    handles.forEach((handle) => {
      ctx.beginPath();
      ctx.rect(handle.x - handleSize / 2, handle.y - handleSize / 2, handleSize, handleSize);
      ctx.fill();
      ctx.stroke();
    });

    ctx.restore();
  }

  private getMousePos(event: MouseEvent, context: ToolContext): { x: number; y: number } {
    const rect = context.canvas.getBoundingClientRect();
    const scaleOffset = context.artboardViewModel.state.scaleOffset;
    const artboardDimensions = context.artboardViewModel.state.boardDimensions;

    const point = ScaleOffset.screenToCanvasPoint(
      event.clientX,
      event.clientY,
      rect,
      scaleOffset,
      artboardDimensions,
    );

    return { x: point.x, y: point.y };
  }

  private calculateBezierBoundingBox(path: Path) {
    const { vertices } = path;

    if (vertices.length === 0) {
      return { minX: 0, minY: 0, maxX: 0, maxY: 0 };
    }

    let minX = Infinity;
    let minY = Infinity;
    let maxX = -Infinity;
    let maxY = -Infinity;

    for (let i = 0; i < vertices.length; i++) {
      const vertex = vertices[i];
      const basePoint = vertex.base;

      // Update min and max with base point
      minX = Math.min(minX, basePoint.x);
      minY = Math.min(minY, basePoint.y);
      maxX = Math.max(maxX, basePoint.x);
      maxY = Math.max(maxY, basePoint.y);

      let nextVertex: Vertex | null = null;
      if (i < vertices.length - 1) {
        nextVertex = vertices[i + 1];
      } else if (path.closed) {
        nextVertex = vertices[0]; // If closed, connect back to the first vertex
      }

      if (nextVertex) {
        const p0 = vertex.base;
        const p1 = vertex.controlRight || vertex.base;
        const p2 = nextVertex.controlLeft || nextVertex.base;
        const p3 = nextVertex.base;

        const bbox = this.getCubicBezierBoundingBox(p0, p1, p2, p3);

        minX = Math.min(minX, bbox.minX);
        minY = Math.min(minY, bbox.minY);
        maxX = Math.max(maxX, bbox.maxX);
        maxY = Math.max(maxY, bbox.maxY);
      }
    }

    return { minX, minY, maxX, maxY };
  }

  private getCubicBezierBoundingBox(p0: Point, p1: Point, p2: Point, p3: Point) {
    const tValues = [];
    const xValues = [p0.x, p3.x];
    const yValues = [p0.y, p3.y];

    for (let i = 0; i <= 1; i++) {
      const b =
        6 * (i === 0 ? p0.x : p0.y) - 12 * (i === 0 ? p1.x : p1.y) + 6 * (i === 0 ? p2.x : p2.y);
      const a =
        -3 * (i === 0 ? p0.x : p0.y) +
        9 * (i === 0 ? p1.x : p1.y) -
        9 * (i === 0 ? p2.x : p2.y) +
        3 * (i === 0 ? p3.x : p3.y);
      const c = 3 * (i === 0 ? p1.x : p1.y) - 3 * (i === 0 ? p0.x : p0.y);

      if (Math.abs(a) < 1e-12) {
        if (Math.abs(b) < 1e-12) continue;
        const t = -c / b;
        if (t > 0 && t < 1) tValues.push(t);
        continue;
      }

      const b2ac = b * b - 4 * c * a;
      if (b2ac < 0) continue;
      const sqrtb2ac = Math.sqrt(b2ac);

      const t1 = (-b + sqrtb2ac) / (2 * a);
      if (t1 > 0 && t1 < 1) tValues.push(t1);

      const t2 = (-b - sqrtb2ac) / (2 * a);
      if (t2 > 0 && t2 < 1) tValues.push(t2);
    }

    for (let j = 0; j < tValues.length; j++) {
      const t = tValues[j];
      const mt = 1 - t;
      const x =
        mt * mt * mt * p0.x + 3 * mt * mt * t * p1.x + 3 * mt * t * t * p2.x + t * t * t * p3.x;
      const y =
        mt * mt * mt * p0.y + 3 * mt * mt * t * p1.y + 3 * mt * t * t * p2.y + t * t * t * p3.y;

      xValues.push(x);
      yValues.push(y);
    }

    const minX = Math.min(...xValues);
    const minY = Math.min(...yValues);
    const maxX = Math.max(...xValues);
    const maxY = Math.max(...yValues);

    return { minX, minY, maxX, maxY };
  }

  private getObjectBoundingBox(obj: ArtboardObject) {
    if ('vertices' in obj) {
      // It's a Path
      const path = obj as Path;
      const boundingBox = this.calculateBezierBoundingBox(path);

      // Apply scale and offset
      const minX = boundingBox.minX * path.scaleOffset.scaleX + path.scaleOffset.x;
      const minY = boundingBox.minY * path.scaleOffset.scaleY + path.scaleOffset.y;
      const maxX = boundingBox.maxX * path.scaleOffset.scaleX + path.scaleOffset.x;
      const maxY = boundingBox.maxY * path.scaleOffset.scaleY + path.scaleOffset.y;

      return { minX, minY, maxX, maxY };
    } else if ('image' in obj) {
      // It's an ImageObject
      const imageObj = obj as ImageObject;
      const minX = imageObj.scaleOffset.x;
      const minY = imageObj.scaleOffset.y;
      const maxX = imageObj.scaleOffset.x + imageObj.image.width * imageObj.scaleOffset.scaleX;
      const maxY = imageObj.scaleOffset.y + imageObj.image.height * imageObj.scaleOffset.scaleY;

      return { minX, minY, maxX, maxY };
    } else {
      return { minX: 0, minY: 0, maxX: 0, maxY: 0 };
    }
  }

  private getResizeHandles(boundingBox: {
    minX: number;
    minY: number;
    maxX: number;
    maxY: number;
  }) {
    const { minX, minY, maxX, maxY } = boundingBox;
    const midX = (minX + maxX) / 2;
    const midY = (minY + maxY) / 2;

    return [
      { x: minX, y: minY, position: 'nw' },
      { x: midX, y: minY, position: 'n' },
      { x: maxX, y: minY, position: 'ne' },
      { x: maxX, y: midY, position: 'e' },
      { x: maxX, y: maxY, position: 'se' },
      { x: midX, y: maxY, position: 's' },
      { x: minX, y: maxY, position: 'sw' },
      { x: minX, y: midY, position: 'w' },
    ];
  }

  private getHandleUnderMouse(
    event: MouseEvent,
    context: ToolContext,
    obj: ArtboardObject,
  ): ResizeHandle | null {
    const { x, y } = this.getMousePos(event, context);
    const boundingBox = this.getObjectBoundingBox(obj);
    const handles = this.getResizeHandles(boundingBox);

    const scaleOffset = context.artboardViewModel.state.scaleOffset;
    const handleSize = this.handleSize / scaleOffset.scaleY;

    for (const handle of handles) {
      const hx = handle.x;
      const hy = handle.y;

      if (
        x >= hx - handleSize / 2 &&
        x <= hx + handleSize / 2 &&
        y >= hy - handleSize / 2 &&
        y <= hy + handleSize / 2
      ) {
        return handle.position as ResizeHandle;
      }
    }

    return null;
  }

  private getFixedPoint(
    boundingBox: { minX: number; minY: number; maxX: number; maxY: number },
    handle: ResizeHandle,
  ) {
    const { minX, minY, maxX, maxY } = boundingBox;
    switch (handle) {
      case 'nw':
        return { x: maxX, y: maxY };
      case 'n':
        return { x: (minX + maxX) / 2, y: maxY };
      case 'ne':
        return { x: minX, y: maxY };
      case 'e':
        return { x: minX, y: (minY + maxY) / 2 };
      case 'se':
        return { x: minX, y: minY };
      case 's':
        return { x: (minX + maxX) / 2, y: minY };
      case 'sw':
        return { x: maxX, y: minY };
      case 'w':
        return { x: maxX, y: (minY + maxY) / 2 };
      default:
        return { x: 0, y: 0 };
    }
  }
}
